* {
	box-sizing: border-box;
}

html, body {
	height: 100vh;
	overflow: hidden;
	width: 100vw;
}

body {
	background: url("/../images/chalkboardBackground.jpg") no-repeat center center fixed;
	background-size: cover;
}

h3 {
	padding-top: 0.5em;
	font-size: 1.2em;
}

h6 {
	color: #000;
	padding-bottom: 0.6em;
	margin: 0.5em;
}

.allNames {
	color: blue;
	margin: 0em;
	padding-left: 0.5em;
}

.app {
	height: 100vh;
	overflow: auto;
	width: 100vw;
}

.clickable, .clickable:hover, ul li:hover {
	cursor: pointer;
}

.content-container {
	min-height: 100vh;
	position: relative;
	top: 64px;
	width: 100vw;
}

.dashboard-content-row {
	margin: auto;
	max-width: 1080px;
	padding: 20px 0;
	width: 90%;
}

.loginContentRow, .registerContentRow {
	margin: 0;
	padding: 0;
}

.infoContainer {
	display: block;
	margin: auto;
	margin-top: 1.8em;
	max-width: 90%;
	border: 2px solid silver;
}

.info-div {
	max-height: 300px;
	overflow: hidden;
}

.infoInfoBox {
	border-top: 2px solid silver;
	height: 14em;
	background-color: #fff;
}

.infoTitle {
	text-align: center;
	background-color: #d00010;
	color: #fff;
}

.list-group-item {
	text-transform: uppercase;
	font-size: 0.8em;
}

.modal {
	margin-bottom: 80px;
	margin-top: 80px;
}

.modal-body {
	padding: 2em 2em;
}

.modal-dialog {
	margin-top: 0;
}

.modal-header {
	background-color: #0000FF;
}

.modal-title {
	padding-left: 0.7em;
	color: #fff;
}

#editIcon {
	width: 0.7em;
	height: auto;
	position: absolute;
	right: 0.4em;
	top: 0.4em;
}

#mainNavbar {
		background-color: #d00010;
		left: 0;
		min-height: 4em;
		padding: 0.5em;
		position: fixed;
		top: 0;
		width: 100%;
		z-index: 9;
}

#navTitle {
		text-decoration: none;
		color: #fff;
		font-size: 1.3em;
}

#navLogo {
		height: 2.3em;
		margin: 0em 1em;
		padding-left: 0em;
		width: auto;
}

#navLogoutLink {
	border-top: 1px solid gray;
}

#navDropdownMenu {
		margin-right: 1.5em;
		font-size: 1.3em;
}

#navbarDropdown {
		color: #fff;
}

#search-container {
	left: 0;
	position: fixed;
    top: 4em;
	width: 100%;
	z-index: 2;
}
a, a:visited, a:hover {
	color: #000;
	text-decoration: none;
}

.uploadButtonDiv {
	margin-bottom: 20px;
}
input {
	display: block;
	margin: auto;
	margin-bottom: 10px;
	width: 100%;
}

#forgotPassword {
	display: block;
	font-size: 12px;
	margin: auto;
	opacity: 0.7;
	text-align: center;
	width: 70%;
}

#login-form #formTitle {
	color: #393433;
	margin-bottom: 20px;
	margin-top: 40px;
	text-align: center;
}

#login-form {
	background-color: #fff;
	border: 2px solid #393433;
	border-radius: 4px;
	float: left;
	height: 400px;
	margin-left: 20px;
	margin-top: 80px;
	padding: 20px;
	width: 400px;
}

#login-submit {
	display: block;
	margin: auto;
	margin-top: 20px;
	width: 100px;
}

#password {
	margin-bottom: 0;
}

#registerLink {
	display: block;
	margin: auto;
	margin-top: 20px;
	text-align: center;
}

@media only screen and (max-width: 998px) {
	#login-form #formTitle {
		margin-top: 20px;
	}

	#login-form, #largeLogo {
		height: 340px;
		width: 340px;
	}
}

@media only screen and (max-width: 767px) {
	.largeLogo {
		display: none;
	}

	#login-form {
		float: none;
		height: 360px;
		margin: auto;
		margin-top: 50px;
		width: 80%;
	}

	#login-form #formTitle {
		margin-top: 30px;
	}
}
input {
	display: block;
	margin: auto;
	margin-bottom: 10px;
	width: 70%;
}

.col-md-6 {
	margin: 0px;
	padding: 0px;
}

#confirm-password {
	margin-top: 10px;
}

#register-form #formTitle {
	color: #393433;
	margin-top: 20px;
	margin-bottom: 20px;
	text-align: center;
}

#largeLogo {
	float: right;
	height: 400px;
	margin-right: 20px;
	margin-top: 80px;
	width: 400px;
}

#loginLink {
	display: block;
	margin: auto;
	padding: 20px;
	text-align: center;
}

#registerBtn {
	display: block;
	margin: auto;
	margin-top: 20px;
	width: 40%;
}

#register-form {
	background-color: #fff;
	border: 2px solid #393433;
	border-radius: 4px;
	float: left;
	height: 400px;
	margin-left: 20px;
	margin-top: 80px;
	padding: 20px;
	width: 400px;
}

@media only screen and (max-width: 998px) {
	#loginLink {
		padding-top: 10px;
	}
	
	#register-form #formTitle {
		margin-top: 0px;
	}

	#register-form, #largeLogo {
		height: 340px;
		width: 340px;
	}
}

@media only screen and (max-width: 767px) {
	.largeLogo {
		display: none;
	}

	#register-form {
		float: none;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
		margin: auto;
		margin-bottom: 40px;
		margin-top: 40px;
		width: 80%;
	}

	#register-form #formTitle {
		margin-top: 10px;
	}
}
.table {
	background-color: #FFF;
	position: absolute;
	left: 0;
	top: 3.3em;
}

tbody {
	font-size: 0.8em;
}

ul {
	list-style-type: none;
	padding: 0;
}
input {
	display: block;
	margin: auto;
	margin-bottom: 10px;
	width: 100%;
}

#reset-form .class-title {
	color: #393433;
	margin-bottom: 20px;
	margin-top: 40px;
	text-align: center;
}

#reset-form {
	background-color: #fff;
	border: 2px solid #393433;
	border-radius: 4px;
	float: left;
	height: 400px;
	margin-left: 20px;
	margin-top: 80px;
	padding: 20px;
	width: 400px;
}

#reset-submit {
	display: block;
	margin: auto;
	margin-top: 20px;
	width: 100px;
}

@media only screen and (max-width: 998px) {
	#reset-form .reset-title {
		margin-top: 20px;
	}

	#reset-form, #largeLogo {
		height: 340px;
		width: 340px;
	}
}

@media only screen and (max-width: 767px) {
	.largeLogo {
		display: none;
	}

	#reset-form {
		float: none;
		height: 360px;
		margin: auto;
		margin-top: 50px;
		width: 80%;
	}
}
